            trigger: 'axis',
            formatter: function (params) {
              return params[0].axisValueLabel + " : "+params[0].data;
            },
            axisPointer: {
                animation: false
            }
        },
        xAxis: {
          name: "시간(요일)",
          nameLocation: 'center',
          nameTextStyle: {
            padding: 8,
            fontSize: 9
          },
          data: ['일', '월', '화', '수', '목', '금', '토']
        },
        yAxis: {
          name: "발전량(KWh)",
          nameLocation: 'center',
          nameTextStyle: {
            padding: 8,
            fontSize: 9
          },
        },
        grid: {
          left: '5%',
          right: '0%',
          bottom: '5%',
          top: '15%',
          containLabel: true
        },
        dataGroupId: '',
        animationDurationUpdate: 500,
        series: {
          type: 'bar',
          data: POWER1,
          universalTransition: {
            enabled: true,
            divideShape: 'clone'
          }
        }
      }
    }
  },
  created() {
    
  },
  mounted() {
    setTimeout(() => {
      this.$store.commit('setS', {s0:false, s1:false, s2:true, s3:false, s4:false})
    }, 500)
    var chartDom = this.$refs.windChart
    this.wind = echarts.init(chartDom)
    this.wind.setOption(this.wind_option)
    chartDom = this.$refs.powerChart
    this.power = echarts.init(chartDom)
    this.power.setOption(this.power_option)
    
  },
  destroyed() {
    this.$store.commit('setS', {s0:false, s1:false, s2:false, s3:false, s4:false})
  },
  computed: {
  },
  methods: {
    goStat() {
      this.$router.push('/stat')
    },
    setWeek(week) {
      this.week = week

      switch (week) {
        case 1:
          this.wind_option.series[0].data = WIND1
          this.power_option.series.data = POWER1
          this.date = DATE1
          break;
        case 2:
          this.wind_option.series[0].data = WIND2
          this.power_option.series.data = POWER2
          this.date = DATE2
          break;
        case 3:
          this.wind_option.series[0].data = WIND3
          this.power_option.series.data = POWER3
          this.date = DATE3
          break;
        case 4:
          this.wind_option.series[0].data = WIND4
          this.power_option.series.data = POWER4
          this.date = DATE4
          break;
        case 5:
          this.wind_option.series[0].data = WIND5
          this.power_option.series.data = POWER5
          this.date = DATE5
          break;
      }
      this.wind.setOption(this.wind_option)
      this.power.setOption(this.power_option)
    },
    selectedClass(week) {
      if (this.week === week) {
        return 'selected'
      }
      return ''
    }
  }
}
</script>
